.edit-button {
    background-color: #F6BD60;
    border-radius: .5rem;
    padding: .4rem;
    width: 1.5rem;
    height: auto;
    display: flex;
    align-items: center;
    justify-content: center;
}

.delete-button {
    background-color: #EF3E36;
    border-radius: .5rem;
    padding: .4rem;
    width: 1.5rem;
    height: auto;
    display: flex;
    align-items: center;
    justify-content: center;
}

.long-button {
    width: 13rem;
    cursor: pointer;
    border-radius: 10px;
    text-decoration: none;
    font-family: 'Special Elite';
    display: flex;
    align-items: center;
    justify-content: center;
    box-shadow: 0px 4px 4px rgb(0, 0, 0, 0.25);
    padding: 2px;
}